import React from "react";
import '../styles/pages/login.css'
export default function Login() {
    return (
        <div className="login-main">
            <h1>Sign In</h1>
            <form className="w-100">
                <div class="form-group row">
                    <label for="inputEmail3" class="col-sm-1 offset-4 col-form-label">Email</label>
                    <div class="col-sm-7 w-25">
                        <input type="email" class="form-control" id="inputEmail3" placeholder="Email" />
                    </div>
                </div>
                <div class="form-group row mt-2">
                    <label for="inputPassword3" class="col-sm-1 offset-4 col-form-label">Password</label>
                    <div class="col-sm-7 w-25">
                        <input type="password" class="form-control" id="inputPassword3" placeholder="Password" />
                    </div>
                </div>
                <div class="form-group row">
                    <div class="col-6 offset-6 mt-2">
                        <button type="submit" class="btn btn-primary">Sign in</button>
                    </div>
                </div>
            </form>
        </div>
    );
}